.bbox{
  width: 500px;
  height: 77px;
  ul{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    li{
      margin-right: 20px;
      float: left;
      list-style: none;
      width: 61px;
      height: 77px;
      text-align: center;
      span{
        display: inline-block;
        width: 61px;
        height: 53px;
      }
      p{
        color: #ffffff;
        font-size: 14px;
      }
    }
  }
}